import '../css/HomeTop.css';
import {Link} from "react-router-dom";
import {useEffect, useRef, useState} from "react";

function HomeTop() {
    const [isOpen, setIsOpen] = useState(false);
    const domPilihJenjang = useRef();
    useEffect(()=> {
        if (domPilihJenjang.current !== null && isOpen) {
            domPilihJenjang.current.style.display = "block";
        }
    },[isOpen]);

    const ModalPilihJenjang = ()=>{
        if(isOpen){
            return (<div ref={domPilihJenjang} className={'modal'}>
                <div className={'modal-pilih-jenjang-inner'}>
                    <div className={'header-pilih-jenjang'}></div>
                    <div className={'body-pilih-jenjang'}>
                        <p>Silahkan pilih jenjang</p>
                        <Link className={'btn-jenjang'} to={'/prediksi/mi'}><i className="fa-solid fa-child"></i> &nbsp; Madrasah Ibtidaiyah </Link>
                        <Link className={'btn-jenjang'} to={'/prediksi/mts'}><i className="fa-solid fa-child-reaching"></i>&nbsp;Madrasah Tsanawiyah</Link>
                        <Link className={'btn-jenjang'} to={'/prediksi/ma'}><i className="fa-solid fa-user"></i>&nbsp;Madrasah Aliyah</Link>
                        <a className={'btn-jenjang'} onClick={()=>{setIsOpen(false)}}><i
                            className="fa-solid fa-right-from-bracket"></i>&nbsp;Tutup</a>
                    </div>
                </div>

            </div>)
        }
        return '';
    }

    return (
        <>
            <ModalPilihJenjang/>
            <div className='top-wrapper'></div>
            <div className='top-container'>
                <h1>Deteksi dan Mitigasi</h1>
                <h1>Learning Loss</h1>
                <p>Deteksi dan Mitigasi Learning loss pada pendidikan agama Islam di madrasah dengan pedekatan Sains Data </p>
                <a onClick={()=>{alert('Under Constraction')}} className={'btn-home'}>Apa itu Learning Loss&nbsp;<i className="fa-solid fa-circle-arrow-right"></i></a>
                <a onClick={()=>{setIsOpen(true)}} className={'btn-home'}>Mulai Prediksi&nbsp;<i className="fa-solid fa-circle-arrow-right"></i></a>
            </div>

        </>
    );
}

export default HomeTop;